/**
Applies a flex display value to a wrapping element. Additionally, add how many columns you want your flex 
wrapper to have, e.g. 'flex-wrapper--three-columns'. Ensure 'flex-wrapper__content' class is applied to
to first-level child elements for flex-basis rules to be correctly applied.
Example HTML:
    <ul class="cta-block flex-wrapper flex-wrapper--three-columns">
        <li class="flex-wrapper__content">
            First list item content
        </li>
        <li class="flex-wrapper__content">
            Second list item content
        </li>
        <li class="flex-wrapper__content">
            Third list item content
        </li>
    </ul>
*/
@media (min-width: 769px) {
    .flex-wrapper {
        display: flex;
    }
    .flex-wrapper.flex-wrapper--two-columns > .flex-wrapper__content {
        flex-basis: 50%;
    }
    .flex-wrapper.flex-wrapper--three-columns > .flex-wrapper__content {
        flex-basis: 33%;
    }
    .flex-wrapper.flex-wrapper--four-columns > .flex-wrapper__content {
        flex-basis: 25%;
    }
    .flex-wrapper .flex-wrapper__content:not(:last-of-type) {
        margin-right: 0.5rem;
    }
}

